.main {
    background-color: var(--primary-text-color);
}
.film-list__item {
    margin-bottom: 20px;
}
.film-list__img {
    width: 280px;
    height: 400px;
    border-radius: 5px;
    margin-bottom: 10px;
    cursor: pointer;
    object-fit: cover;
}
.film-list__title {
    font-weight: 500;
    font-size: 12px;
    line-height: 1.16;
    text-transform: uppercase;
    color: var(--secondary-text-color);
}
.film-list__text {
    font-weight: 500;
    font-size: 12px;
    line-height: 1.33;
    color: var(--accent-text-color);
}
@media screen and (max-width: 767px) {
    .main {
        padding-top: 20px;
        padding-bottom: 40px;
        background-color: var(--primary-text-color);
    }
}

@media screen and (min-width: 768px) and (max-width: 1279px) {
    .main {
        padding-top: 60px;
    }
    .film-list {
        display: flex;
        flex-wrap: wrap;
        width: 100%;
        justify-content: space-between;
    }
    .film-list__item {
        flex-basis: calc((100% - 32px) / 2);
    }
    .film-list__img {
        width: 336px;
        height: 495px;
    }
    .film-list__item {
        margin-bottom: 32px;
    }
}
@media screen and (min-width: 1280px) {
    .film-list {
        display: flex;
        flex-wrap: wrap;
        margin-left: -16px;
        margin-top: -32px;
    }
    .film-list__item {
        flex-basis: calc((100% - 48px) / 3);
        margin-left: 16px;
        margin-top: 32px;
    }
    .film-list__img {
        width: 395px;
        height: 574px;
    }
    .main {
        padding-top: 60px;
    }

}



